import React, {Component} from 'react';

import classes from './Carousels.module.css';
import Carousel from './Carousel/Carousel';
import TestimonialData from './TestimonialData';


class Carousels extends Component{

    state ={
        carouselItems : TestimonialData ,
        carouselitem : 0
    }

    nextTestimonialHandler = () =>{
        let curentIndex = this.state.carouselitem;
        const itemsLength = this.state.carouselItems.length;
        let newIndex = curentIndex + 1;
        if( newIndex >= itemsLength){
            newIndex = 0;
        }

        this.setState({carouselitem: newIndex});
    }

    previousTestimonialHandler = () =>{
        let curentIndex = this.state.carouselitem;
        let newIndex = curentIndex - 1;
        if( newIndex < 0){
            newIndex = this.state.carouselItems.length - 1;
        }

        this.setState({carouselitem: newIndex});
    }
    


    render() {

        const clientIndex = this.state.carouselitem;
        const cliendData = {...this.state.carouselItems[clientIndex]};
        const carousel = (
            <Carousel data={cliendData}/>
        );

        return(
            <div className={classes.Carousel_Container}>
                <div className={[classes.Btn, classes['left']].join(' ')}  onClick={this.previousTestimonialHandler}></div>
                    {carousel}
                <div className={[classes.Btn, classes['right']].join(' ')} onClick={this.nextTestimonialHandler}></div>
    
            </div>
        );
    }
}


export default Carousels;