import React from 'react';

import classes from './Footer.module.css';
import SocialIcons from '../SocialIcons/SocialIcons';
import footerimg from '../../assests/images/footer/footer.svg';
import Aux from '../../hoc/Auxliary/Auxiliary';
const footer = (props) => {
    return (
        <Aux>
            
            <footer className={classes.Footer} id="footer">
                <div className={classes.Image_Container}>
                    <img src={footerimg} alt="footerImage"/>
                </div>
                <div className={classes.Footer_Social_Container}>
                    <div className={classes.Footer_Icons}>
                        <h3>Find me in social media :</h3> 	
                        <SocialIcons/>
                    </div>
                <p className={classes.Copyright}>Copyright 2020 Peter Bilolo Badibake</p>
                </div>
        </footer>
        </Aux>
        
    );
}

export default footer;