import React from 'react';

import {SocialIcon} from 'react-social-icons';
import classes from './SocialIcon.module.css';

const socialIcon = (props)  =>{
    return(
            
            <SocialIcon 
            hey={props.icon}
            network={props.icon}
            bgColor="none"
            fgColor="#FFFFFF"
            url={props.configuration.link} 
            className={[classes.SocialIcon, classes[props.icon]].join(' ')}
            />
      
    );
}


export default socialIcon;