import React from 'react';

import classes from './About.module.css';
import AboutMeIcon from '../../assests/images/button/aboutButton.svg';
import {NavHashLink as NavLink } from 'react-router-hash-link';


const About = (props) => {
    return (
        <div className={classes.About} id="home">
            <h4 className={classes.Logo}>Peter <br/> Bilolo Badibake</h4>
            <div className={classes.About_text}>
                <h1 className={classes.My_heading}>Do you own a business?<br/> Would you like to Digitalize it ? <br/>Make your live eaiser and reach a wide audience</h1>   
            </div>
            <NavLink className={classes.About_Icon} to='#about' smooth><img src={AboutMeIcon} alt="Downbutton"/></NavLink>
        </div>
    );
}

export default About ;