import React, {Component} from 'react';

import Aux from '../../../hoc/Auxliary/Auxiliary';
import Input from '../../UI/Input/Input';
import classes from './ContactForm.module.css';
import Button from '../../UI/Button/Button';
import axios from '../../../axios.js'
import FlashMessage from './FlashMessage/FlashMessage';
import Spinner from '../../Spinner/Spinner';

const yup = require('yup');

class  ContactForm extends Component{

    state = {
        messageForm  :{
            name : {
                elementType: 'input',
                elementConfig: {
                  type: 'text',
                  name: 'name',
                  placeholder: 'Your name',
                },
                value: '',
                touched: false,
                isValid : null,
                errormessage : 'Enter name  up to 255 characters'
                },
            email : {
                elementType: 'input',
                elementConfig: {
                    name: 'email',
                    type: 'email',
                    placeholder: 'Your email'
                },
                value: '',
                touched: false,
                isValid : null,
                errormessage : 'Enter a valid email'
                },
            message : {
                    elementType: 'textarea',
                    elementConfig: {
                        name: 'message',
                        cols:'70',
                        rows:'10',
                        placeholder: 'Write your message',
                        maxLength:'500'
                    },
                    value: '',
                touched: false,
                isValid: null,
                errormessage : 'Enter a message of 5 up to 500 characters'      
                }
        },
        messageSent : false,
        submittingForm : false 
    }
    

    inputChangedHandler = async function (messageInputIdentifier, event)  {
        const updatedMessageForm= {...this.state.messageForm};
        const updatedFormElement = {...updatedMessageForm[messageInputIdentifier]} ;
        updatedFormElement.value = event.target.value;
        updatedFormElement.touched = true;
        const validationFieldResult  = await this.validateInpudHandler(messageInputIdentifier, event.target.value);
        updatedFormElement.isValid = validationFieldResult[messageInputIdentifier];
        updatedMessageForm[messageInputIdentifier] = updatedFormElement;

        this.setState({messageForm: updatedMessageForm});
    }

   handleSubmit = async function (event) {
       event.preventDefault();
       const validationResult = await this.validateInpudHandler();
       const unvalidatedField = [];

       for(let field in validationResult){
           if(validationResult[field] === false){
               unvalidatedField.push(field);
           }
       }


       if(unvalidatedField.length === 0){
                 return this.sendMessageHandler();
       }else {
           const messageFields = {...this.state.messageForm};

           for (let field  of unvalidatedField) {
               messageFields[field]= {...messageFields[field], touched: true};
           }

          this.setState({messageForm: messageFields});


       }
   }


   //responsible for sending mails 
   sendMessageHandler = () => {
       // activate spiner
       if(this.state.submittingForm){
           return null;
       }
       this.setState({submittingForm: true});
       console.log('submitting 1');
       const userData = {
           name  : this.state.messageForm.name.value,
           email : this.state.messageForm.email.value,
           message : this.state.messageForm.message.value,
       }

           axios.post('/message/send', userData).then(res => {
           const newFormState = {...this.state.messageForm}
           newFormState.name = {...newFormState.name, touched: false, isValid: false, value: ''};
           newFormState.email = {...newFormState.email, touched: false, isValid: false, value: ''};
           newFormState.message = {...newFormState.message, touched: false, isValid: false, value: ''};
           this.setState({messageForm: newFormState, messageSent: true, submittingForm: false});


           // disable the flash message 
           setTimeout(() =>{
               this.setState({messageSent: false, submittingForm : false});
           }, 3000)
           //remove spinner

       }).catch(error =>{
        //remove spinner
        this.setState({submittingForm: false}) ;  
        if(error.response && error.response.status === 400){

            //invalid form case , revalidate form enfore

               const rejectedData = [...error.response.data.formErrors] ;

               const newFormState =  {...this.state.messageForm}
               for(let rejectField of rejectedData){
                   newFormState[rejectField.param] = {...newFormState[rejectField.param], touched: true, isValid: false, value: rejectField.value};
               }
               this.setState({messageForm: newFormState});
           }


           // here handle everything else like default behavoiur exprecially guards
       })
   }

   messageSchemaValidationHandler = (userData) => {

    let schema = yup.object().shape({
        name : yup.string().trim().max(300).test('min-1', 'not  empty', value => value === ''  ? false : true).required(),
        email: yup.string().email().required(),
        message: yup.string().trim().max(500).min(4).required()
    });
    
    const validationFormResult = {
        name : true,
        email : true,
        message: true
    };

    return schema.validate(userData, 
        {
            abortEarly: false,
            strict: false,
        }).then(  value => {
        return validationFormResult;
        
        
    }).catch( err =>{
        const nonValidateFields = [...new Set(err.inner.map(field => field['path']))];
        
        for(let field of nonValidateFields){
            validationFormResult[field] = false;
        }
        return validationFormResult;
    })
   }

   validateInpudHandler = async function  (updatedField = '', value=null)  {

    const userData = {
        name: this.state.messageForm.name.value,
        email: this.state.messageForm.email.value,
        message: this.state.messageForm.message.value
    };

    if(updatedField !== ''){
        userData[updatedField] = value;
    }

    const validatedFields = await this.messageSchemaValidationHandler(userData);
    return validatedFields;
   }
 
    render(){    
        const formElementArray = [];
            for(let key in this.state.messageForm){
                formElementArray.push({
                    messageInputIdentifier : key,
                    config : this.state.messageForm[key],
                    value: this.state.messageForm[key].value,
                    touched : this.state.messageForm[key].touched,
                    isValid : this.state.messageForm[key].isValid,
                    errorMessage : this.state.messageForm[key].errormessage,
                });
        }
        
        const form = (
            <form onSubmit={(e) =>this.handleSubmit(e)} noValidate style={{ position: "relative"}}>
                {this.state.submittingForm ? <Spinner/> : null}
                <div className={classes.Form}>
                    <div className={classes.First_Form_container}>
                        {formElementArray.filter( elelement => {
                            return elelement.messageInputIdentifier !==  'message'
                        }).map( (element, index) =>{
                            return (<Input 
                            key={element.messageInputIdentifier}
                            elementType={element.config.elementType}
                            elementConfig={element.config.elementConfig}
                            value={element.value} 
                            changed={(event) => this.inputChangedHandler(element.messageInputIdentifier, event)}
                            touched={element.touched}
                            isValid={element.isValid}
                            errorMessage={element.errorMessage}
                            />);
                        })}
                    </div>
                    <div className={classes.Second_Form_container}>
                        {formElementArray.filter( elelement => {
                            return elelement.messageInputIdentifier ===  'message'
                        }).map( (element, index) =>{
                            return (<Input 
                            key={element.messageInputIdentifier}
                            elementType={element.config.elementType}
                            elementConfig={element.config.elementConfig}
                            value={element.value} 
                            changed={(event) => this.inputChangedHandler(element.messageInputIdentifier, event)}
                            touched={element.touched}
                            isValid={element.isValid}
                            errorMessage={element.errorMessage}
                            />);
                        })}
                    </div>
                </div>

                <Button isSubmit>Send Meesage</Button>
            </form>
        );

        return(
            <Aux>
                {this.state.messageSent ? <FlashMessage message="message sent " valid={true}/> : null}
                {form}
            </Aux>
    );
}
}


export default ContactForm;