import React from 'react';

import classes from './NavigationItems.module.css'
import NavigationItem from  './NavigationItem/NavigationItem';

// images
import homeIcon from '../../../assests/images/navigation/house.svg';
import aboutMeIcon from '../../../assests/images/navigation/AboutMeIcon.svg';
import servicesIcon from '../../../assests/images/navigation/servicesIcon.svg';
import contactIcon from '../../../assests/images/navigation/contactIcon.svg';
import testimonialIcon from '../../../assests/images/navigation/testimonialsIcon.svg';
import portofolioIcon from '../../../assests/images/navigation/portfolioIcon.svg';

const navigationItems = (props) =>{
    const links = [
        {name: "Home", link: "/", image: homeIcon},
        {name: "About", link: "/#about", image: aboutMeIcon},
        {name: "Services", link: "/#services", image: servicesIcon},
        {name: "Contact", link: "/#contact", image: contactIcon},
        {name: "Testimonials", link: "/#testimonial", image: testimonialIcon},
        {name: "Portofolio", link: "/#portofolio", image: portofolioIcon, },
        // {name: "Blog", link: "/blog", image: portofolioIcon}

      ];
      
      const navigationLinks = links.map(link => {
        return (
            <NavigationItem key={link.name} path={link.link} icon={link.image} close={props.close ? props.close : null} normal={link.normal}>{link.name}</NavigationItem>
        );
    });  
      
    return (
        
        <ul className={classes.NavigationItems}>
            {navigationLinks}
        </ul>
    )
}


export default navigationItems ;