import React from 'react';

import classes from './CreatePostControl.module.css';
import Button from '../CreatePostButtons/Button';

const createPostControl = (props) => {


    return (
        <div className={classes.CreatePostControl}>
            <div>
                <Button>remove paragrah</Button>
                <Button handler={props.addparagraph}>add paragrah</Button>
            </div>
            <div>
                <Button>Preview</Button>
                <Button submit>Submit</Button>
            </div>
        </div>
    );
}

export default createPostControl;