import React from 'react';

import classes from './CreatePosts.module.css';
import CreatePostControl from './CreatePostControl/CreatePostControl';
import Input from '../UI/PostInput/PostInput';

class CreatePosts extends React.Component {


    state = {
        postForm: {
            title: {
                value : '',
                type : 'title'
            },
            description: {
                value : '',
                type : 'description'
            },
            paragraph1 : {
                value: '',
                type : 'paragraph'
            }
        },
        position :{
            code : 0,
            paragraph : 1,
            url : 0,
        }

    }

    removeParagrahHandler = (event, key) => {
        const inputFileds = {...this.state.postForm};
        const newPostForm = {};
        for(let oldKey in inputFileds){
            if(oldKey !== key){
                newPostForm[oldKey] = {...inputFileds[oldKey]};
            }
        }

        this.setState({
            postForm: newPostForm
        });
        // decrease the number of -- 
    }


    addParagraphHandler = () => {
        let currentParagraphNumber  = this.state.position.paragraph;
        currentParagraphNumber ++;
        const newParagraphkey = 'paragraph' + currentParagraphNumber;
        const newPostForm = {
            ...this.state.postForm
        }

        newPostForm[newParagraphkey] = {
            value: '',
            type: 'paragraph'
        }

        this.setState({
            position : { ...this.state.position, paragraph: currentParagraphNumber},
            postForm : newPostForm
        })
        console.log(this.state);
    }


    inputValueChangedHandler = (event, key) => {
        const newPostForm = {...this.state.postForm}
        const changeField = {...newPostForm[key], value: event.target.value};
        newPostForm[key] = changeField;

        this.setState({
            postForm : newPostForm
        });
    }

    render(){

        const formInputs =[];
        for(let key in this.state.postForm){
            formInputs.push({...this.state.postForm[key], key: key});
        }

        const formFileds = formInputs.map((element) =>{
            return(
                <Input key={element.key} config={element} changed={(e) => this.inputValueChangedHandler(e, element.key)}  removeparagraph={(e) => this.removeParagrahHandler(e, element.key)}/>
            );
        });

        return (
            <div className={classes.CreatePosts_Contanier}>
                <form action="POST">
                    {formFileds}
                </form>
                <CreatePostControl  addparagraph={this.addParagraphHandler} />
            </div>
        );
    }
}



export default  CreatePosts;