import React from 'react';

import Auxiliary from '../../../hoc/Auxliary/Auxiliary';
import classes from './Input.module.css';

const myInput = (props) =>{
    let Input = null;
    let customClasses  = [];
    if(props.touched){
        if(props.isValid){
            customClasses.push(classes['success']);
        }else {
            customClasses.push(classes['danger']);
        }
    }

    switch(props.elementType){
        case 'input':
            customClasses.push(classes.Input);
            Input =(<div className={classes.feedback_2}>
                        {props.touched && !props.isValid ?  <p>{props.errorMessage}</p> : null}
                        <input   className={customClasses.join(' ')} {...props.elementConfig}  value={props.value} onChange={props.changed}/>
                    </div>);
            break
        case 'textarea':
            customClasses.push(classes.Text_Area);
            Input = (<div className={classes.feedback}>
                        {props.touched && !props.isValid ?   <p>{props.errorMessage}</p> : null}
                        <textarea   className={customClasses.join(' ')}  {...props.elementConfig} value={props.value} onChange={props.changed}></textarea>
                </div>);
            break
        default: 
        customClasses.push(classes.Input);
        Input =(<div className={classes.feedback_2}>
                    {props.touched && !props.isValid ?   <p>{props.errorMessage}</p> : null}
                    <input   className={customClasses.join(' ')} {...props.elementConfig}  value={props.value} onChange={props.changed}/>
                </div>);
    }

    return (
        <Auxiliary>
            {Input}
        </Auxiliary>
    )
} 


export default myInput;