import React from 'react';
import Aux from '../../../hoc/Auxliary/Auxiliary';

import classes from './PostInput.module.css';
import Remove from '../../../assests/images/post/minus.svg';

const postInput = (props) => {

    let input = null;
    const customStyle = [classes.Input];
    const inputFieldType = props.config.type;

    switch(inputFieldType){

        case 'title': 
            customStyle.push(classes['title']);
            input =  <input className={customStyle.join(' ')}  type="text" name="title" placeholder="Title" value={props.config.value} onChange={props.changed}/>;
            break;
        case 'url': 
            customStyle.push(classes['url']);
            input = <span>
                    <input className={customStyle.join(' ')}  type="url" name="url" placeholder="url" value={props.config.value} onChange={props.changed}/>;
                    <img src={Remove} alt="remove" onClick={props.removeparagraph}/>
            </span>
                break;
        case 'paragraph':
            customStyle.push(classes['paragraph']);
            input = <span className={classes.Span}>
                <textarea name="paragraph" id="" cols="30" rows="10" className={customStyle.join(' ')} placeholder="Paragraph" value={props.config.value} onChange={props.changed}></textarea>
                <img src={Remove} alt="remove" onClick={props.removeparagraph}/>
            </span>
            break
        case 'code':
            customStyle.push(classes['code']);
            input =  <span>
                <textarea name="code" id="" cols="30" rows="10" className={customStyle.join(' ')} placeholder="Code" value={props.config.value} onChange={props.changed}></textarea>
                <img src={Remove} alt="remove" onClick={props.removeparagraph}/>
            </span>
            break;
        case 'description': 
            customStyle.push(classes['description']);
            input = <textarea name="description" id="" cols="30" rows="10" className={customStyle.join(' ')} maxLength="200" placeholder="Description" value={props.config.value} onChange={props.changed}></textarea>;
            break;
        default :
            customStyle.push(classes['paragraph']);
            input = <span>
                    <textarea name="paragraph" id="" cols="30" rows="10" className={customStyle.join(' ') } placeholder="Paragraph" value={props.config.value} onChange={props.changed}></textarea>
                    <img src={Remove} alt="remove" onClick={props.removeparagraph}/>
            </span>
    }

    
    return(
        <Aux>
            {input}
        </Aux>
    );
};


export default postInput;