import React from 'react';

import Aux from '../../../hoc/Auxliary/Auxiliary';
import classes from './Button.module.css';


const btn = (props) => {
    const customStyle = [classes.Btn];

    if(props.submit){
        customStyle.push(classes['submit']);
    }
   
    return (
        <Aux>
            <button className={customStyle.join(' ')} onClick={props.handler ? props.handler : null}>{props.children}</button>
        </Aux>
    );
}



export default btn ;