import React from 'react';

import classes from './ContactLink.module.css';

const contactLink = (props)=> {
    let linkType = props.config.type;
    switch(linkType) {
        case 'phone':
        	linkType = <a href={"tel:" +  props.config.data}>+ {props.config.data}</a>
            break;
        case 'mail':
            linkType = <a href={"mailto:" + props.config.data}>{props.config.data}</a>
            break;
        case 'map':
            linkType = <a href={"http://maps.google.com/maps?q=" + props.config.data}  rel="noopener noreferrer"  target="_blank">{props.config.data}</a>
            break;     
        default :
        	linkType = <a href={"tel:" + props.config.data} >{props.config.data}</a>
        }

    return (
        <span className={classes.LinkItem}>
            <img src={props.imageSoruce} alt={props.config.data}/> {linkType}
        </span>
    );
}

export default contactLink;