import React from 'react';

import classes from './Portofolio.module.css';

const portofolio = (props) => {

    const portofolioImageDiv = (
        <div className={classes.Portofolio_Image} >
                <div className={classes.container} >
                    <img src={props.imagePath} alt={props.projectName} className={classes.image}/>
                    <a href={props.data.githubLink} className={classes.overlay}>
                        <div className={classes.text}>
                            <h1>{props.data.projectType}</h1>
                            <p>Branding, Development</p>          
                        </div>
                    </a>
                </div>
            </div>
    );

    const portofolioTextDiv = (
        <div className={classes.Portofolio_Text}>
                <h1>{props.projectName}</h1>
                <p>
                {props.data.projectDescription}
                </p>
            </div>
    );

    let portofolioDirection = props.data.direction === 'left';

    return (
        <div className={classes.Portofolio}>
            {portofolioDirection === true ? portofolioImageDiv : null}
            {portofolioTextDiv}
            {portofolioDirection !== true ? portofolioImageDiv : null}
        </div>
    );
}

export default portofolio;