import React from 'react';

import classes from './Contact.module.css';
import ContactLink from './ContactLink/ContactLink';
import ContactMap from './ContactMap/ContactMap';
// svg images
import phoneIcon from '../../assests/images/contact/phoneIcon.svg';
import locationIcon from '../../assests/images/contact/location.svg';
import emailIcon from '../../assests/images/contact/emailIcon.svg';
import ContactForm from './ContactForm/ContactForm';

const contact = (props) => {

    const contactLinks = [
        {name : 'phone',
        config : {
            type: 'phone',
            data : '48792524065'
        },
        imageSource: phoneIcon 
    },
    {name : 'mail',
        config : {
            type: 'mail',
            data : 'peterbadibake@gmail.com'
        } ,
        imageSource: emailIcon
    },
    {name : 'map',
        config : {
            type: 'map',
            data : 'wrocalw, Poland'
        } ,
        imageSource: locationIcon
    }
    ]

    const newContacLinks = contactLinks.map(contLink => {
        return (
            <ContactLink  key={contLink.name} imageSoruce={contLink.imageSource} config={contLink.config} />
        );
    });
    return (
        <div className={classes.Contact} id="contact">
           <h1 className={classes.Contact_heading}>Contact:</h1>
           <div className={classes.First_Contact__container}>
                <div className={classes.Contact_info}>
                    {newContacLinks}
                </div>
                <div className={classes.Contact_map}>
                    <div className={classes.Map_Container}>
                        <ContactMap />
                    </div>
                </div>
           </div>
           <div className={classes.ContactForm}>
                <ContactForm/>
           </div>
        </div>
    );
}

export default contact;