import React from 'react';

import classes from './Hire.module.css';
import Button from '../UI/Button/Button';
import {NavHashLink as NavLink} from 'react-router-hash-link';

const hire = (props) => {
    return (
        <div className={classes.Hire} id="hire">
             <h2>Want do digitalize your business ? </h2>
             <h3>I am available for freelancing</h3>
             <NavLink to='#contact' smooth>
                <Button >Hire me</Button>
             </NavLink>
        </div>
    );
}

export default hire;