import React from 'react';

import classes from './FlashMessage.module.css';
import Aux from '../../../../hoc/Auxliary/Auxiliary';

const flasMessage = (props) =>{
    const customStyle = [classes.Message] ;
    switch(props.valid){
        case true: 
                customStyle.push(classes['success']);
                break
        default:
                classes.push(classes['error']);
                break
    }

    const message = (
        <div className={customStyle.join(' ')}>
            <b>{props.message}</b>
        </div>
    );
    return (
        <Aux>
            {message}
        </Aux>
    );
}



export default flasMessage;