import React from 'react';

import SocialMedia from './SocialIcon/SocialIcon'
import Aux from '../../hoc/Auxliary/Auxiliary';

const socialIcons = (proos) => {
    const icons  = [
        {
            name : "github",
            config:{legend: 'Like us',
            link : 'https://github.com/bke243'}
        },
        {
            name : "instagram",
            config:{legend: 'Instagram',
            link : 'https://www.instagram.com/peterb_bk'}
        },
        {
            name : "linkedin",
            config:{legend: 'Join us',
            link : 'https://www.linkedin.com/in/peter-bilolo-badibake-a5bb26189/'}
        },
        {
            name : "twitter",
            config:{legend: 'Follow us',
            link : 'https://twitter.com/peterbbke'
        }
        }
    ];

    const medias = icons.map(media => {
        return (
            <SocialMedia key={media.config.legend} configuration={media.config} icon={media.name}/>
        );
    })  ;
    return (
        <Aux>
            {medias}
        </Aux>
    );
}

export default socialIcons ;