import React from 'react';

import classes from './AboutMe.module.css';
import peter from '../../assests/images/peter.jpg'

const aboutMe = (props) => {
    const educations = [
        {institution: "Politechnika Wrocawska, Applied Computer Science", period: "Bachelor, 2018-present", link: 'http://pwr.edu.pl/en/'},
        {institution: "College Saint Louis, Mathematics and Physics", period: "High School", link: '#'},
        
    ];

    const certificates = [
        {name: 'Nodejs ',institution : "Academind ", year: "2020", link: 'https://udemy-certificate.s3.amazonaws.com/image/UC-79e9ff7b-a241-4f37-bf6b-50464dbb48a1.jpg'},
        {name: 'CS50 ',institution : "HAVARDx", year: "2019", link: 'https://courses.edx.org/certificates/96cff4b41e9944e891314a8be8bfb34f'},
    ]
    return (
        <div className={classes.AboutMe}  id="about">
            <div className={classes.AboutMe_Column_1}>
                <div className={classes.AboutMe_Image}>
                    <img src={peter} alt="Myimage"/>
                </div>
                <h4 style={{textShadow:" -8px 8px 13px rgba(0, 0, 0, 0.30)"}}>Peter Bilolo Badibake</h4>
            </div>
            <div className={classes.AboutMe_Column_2}>
                <div className={classes.Bio}>
                    <h1 className={[classes.remove_space, classes.Main_heading].join(' ')}>About me:</h1>
                    <p className={[classes.remove_space].join(' ')}>Hello, My Name is  Peter Bilolo Badibake; I am a Junior python/ full-stack freelancer developer based in Poland.   I  mainly work with the MERN stack but as a quick learner; I am able to adapt myself to multiple technologies depending on the project requirements.  During my free time, 
                    I prefer reading books, drinking tea, doing sport especially football, and explore the new  tech trend to  always keep myself   updated.</p>
                </div>
                <div className={classes.Achievement}>
                    <div className={classes.Education}>
                        <h1 className={[classes.remove_space, classes.Achievement_heading].join(' ')}>My education:</h1>
                        <ul className={classes.Achievement_list}>
                            { educations.map(education => {
                                return(
                                <li key={education.institution}><a href={education.link} target={education?.link !== '#' ? '_blank' : '_self'}  rel="noopener noreferrer">{education.institution} <br/>  ({education.period})</a></li>
                                );
                            })}
                        </ul>
                    </div>
                    <div className={classes.Certification}>
                        <h1 className={[classes.remove_space,  classes.Achievement_heading].join(' ')}>My certificates:</h1>
                        <ul  className={classes.Achievement_list}>
                            {certificates.map(certificate => {
                                return(
                                <li key={certificate.name}><a href={certificate.link} target={certificate?.link !== '#' ? '_blank' : '_self'} rel="noopener noreferrer">{certificate.name}({certificate.institution}, {certificate.year}) </a></li>
                                );
                            })}
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default aboutMe;