import React from 'react';

import Backdrop from '../../UI/Backdrop/Backdrop';
import Aux from '../../../hoc/Auxliary/Auxiliary';
import classes from './SideDrawer.module.css';
import NavigationItems from '../NavigationItems/NavigationItems';

const sideDrawer = (props) => {
    let customedSyle = [classes.SideDrawer, classes.Close];
    if(props.opened){
        customedSyle = [classes.SideDrawer, classes.Open];
    }

    return (
        <Aux >
            <Backdrop show={props.opened} close={props.close} />
            <div className={customedSyle.join(' ')}>
                <span className={classes.Arrow} onClick={props.close}> &#8592;</span>
                <nav>
                    <NavigationItems  close={props.close}/>
                </nav>
            </div>
        </Aux>
    );
}


export default sideDrawer ;