import React from 'react';
import Aux from '../../../hoc/Auxliary/Auxiliary';
import Portofolio from '../Portofolio/Portofolio';

const projects  = (props) => {

    const projectsContainer = [
        { name :'Small Online Shop', 
          imagePath : 'https://images.unsplash.com/photo-1556745757-8d76bdb6984b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=966&q=80',
          data : {
              projectType : 'Go to Github',
              projectDescription: `The project was realized using the following tech stack: express-js web application framework, 
              MongoDB database, and ejs template engine. The main objective of the application was the implementation 
              of functionalities such as user authentication, payment integration,  
              image storage, dynamic pdf generation, and other security aspects such as csrf attacks ... 

              `,
              direction: 'left',
              githubLink: 'https://github.com/bke243/expressjsWebAppSmallShop'
          } 
          
        },
        { name :'React Amazone clone', 
          imagePath : `https://images.unsplash.com/photo-1542744095-291d1f67b221?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80`,
          data : {
              projectType : 'Go to Github',
              projectDescription: `The project was realized using the following tech stack: Reactjs 
              frontend application framework, Firebase for real-time database,
              React Context API  for state management and stripe functionalities to for payment, and firebase cloud function to process stripe payment (severless) add more stuff `,
              direction: 'right',
              githubLink: 'https://github.com/bke243/amazone-clone-with-react'
          } 
        }, 
        { name :'Finance ', 
          imagePath : `https://images.unsplash.com/photo-1563986768711-b3bde3dc821e?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1048&q=80`,
          data : {
              projectType : 'Go to Github',
              projectDescription: ` A small website that allows users to buy and sell stocks. the project was realized using the following tech stack: 
               Flask framework, SQL Database, iex stocks cloud API, and Bootstrap template. the objective of the application was to simulate a trading website.
                when a user creates an account and login; he gets a certain amount of money that will allow him to buy, sell, and check the current price stocks in real-time
                 with the usage of iex API
              `,
              direction: 'left',
              githubLink: 'https://github.com/bke243/Havard-CS50--2019-Solutions/tree/master/Pset8/finance'
          } 
          
        }
    ];
    const boundary = props?.limit ;
    const portofolioItems = projectsContainer.map((portofolio, index) => {
        if(index >= boundary){
            return (null);
        }
        return (<Portofolio key={portofolio['name']} imagePath={portofolio.imagePath} data={portofolio.data} projectName={portofolio.name}/>);
    });

    return(
        <Aux>
            {portofolioItems}
        </Aux>
    )
}


export default projects;