import React from 'react';

import classes from './Service.module.css';


const service = (props) => {
    const customstyle = [classes.Service];

    return (
            
            <div className={customstyle.join(' ')}>    
                <article >
                     <img src={props.image} alt={props.config.alt}/>
                    <h4>{props.title} </h4>
                    <p>{props.config.description}</p>                     
                </article>
            </div>
    )
}

export default service;