import React from 'react' ;

import classes from './Services.module.css';
import Service from  './Service/Service';

import api from '../../assests/images/services/apiIcon.svg';
import backend from '../../assests/images/services/backendIcon.svg';
import webdev from '../../assests/images/services/reactIcon.svg';
import code from '../../assests/images/services/fullstackIcon.svg';


const services = (props) => {
    const servicesInformation = [
        {title: 'React frontend Application ', 
        image: webdev,
        content: {description : 'i can develop / customize responsive and browser-/ device-compatible web frontend application in CSS3, HTML5 and Javascript using React',
                  alt: 'Frontend',
                  displayEnd: true}
        },
        {title: 'Backend Application', 
        image: backend,
        content: {description : 'do you want to develop/add more endpoints to your backend applications? I can help you go through all both process with an happy satisfactory ending',
                  alt: 'backend',
                  displayEnd: false}
        },
        {title: 'Web Applications', 
        image: code,
        content: {description : 'with the MERN stack, I create lean, scalable, performant, secure, and user-friendly Web applications that will meet all your possible requirements',
                  alt: 'Fullstak',
                  displayEnd: true}
        },
        {title: 'RestAPI ', 
        image: api,
        content: {description : 'willing to serve your front-end application with different origin data endpoints? let me help you go through all the using Node Js that creates lean, scalable, and especially secure Rest API endpoints',
                  alt: 'Api',
                  displayEnd: false}
        }
    ];

     const servicesItem = servicesInformation.map( service =>  {
         return ( 
             <Service key={service.title}  config={service.content} title={service.title} image={service.image}/>
         )
    }) ;
    return (
        <div className={classes.Services} id="services">
            <h1 className={classes.Services_heading}>services :</h1>
            <div className={classes.Services_Container}>
                {servicesItem}
            </div>
        </div>
    );
}

export default services ;