import React from 'react';

import classes from './Portofolios.module.css';
import Button from '../UI/Button/Button';
import Projects from './Projects/Projects';
const portofolios = (props) => {

    

    

    return(
        <div className={classes.Portofolios} id='portofolio'>
            <h1 className={classes.Portofolios_heading}>Portofolio:</h1>
            <Projects limit={2}/>
            <Button toLink='/portofolio'>Full Portofolio</Button>
        </div>
    );
}

export default portofolios;