import React from 'react';

import Aux from '../../../hoc/Auxliary/Auxiliary';
import classes from './Button.module.css';


const btn = (props) => {
    let toLink = null;
    if(props.toLink){
        toLink = (event, toLink = props.toLink) =>{
            window.location.href= toLink;
        }
    }
    
    const isSubmit = props.isSubmitButton;
    let myButton = null;
    if(isSubmit){
        myButton =  <button  className={classes.Btn} type="submit" value="submit">{props.children}</button>
    }else {
        myButton =<button  className={classes.Btn} onClick={toLink} >{props.children}</button>
    }

    return (
        <Aux>
            {myButton}
        </Aux>
    );
}



export default btn ;