import React, {Component} from 'react';

import AUX from '../Auxliary/Auxiliary';
import ToolBar from '../../Components/Navigation/ToolBar/ToolBar';
import SideDrawer from '../../Components/Navigation/SideDrawer/SideDrawer';
import classes from './Layout.module.css';
import Footer from '../../Components/Footer/Footer';


class Layout extends Component {

    state ={
        showSideDrawer: false
    }
    
    closeSideDrawerHandler = () => {
        this.setState({showSideDrawer : false});
    }

    drawerToggleClicked = () => {
        this.setState( (prevState) =>{
            return { showSideDrawer : !prevState.showSideDrawer};
        });
    }

    componentDidMount() {
        window.addEventListener("resize", () => {
            if(window.innerWidth > 767 && this.state.showSideDrawer === true){
                this.closeSideDrawerHandler();
            }
        });
    }

    componentWillUnmount() {
        window.removeEventListener("resize", () => {
            if(window.innerWidth > 768 && this.state.showSideDrawer === true){
                this.closeSideDrawerHandler();
            }
        });
    }

    render() {
        return (
            <AUX>
                <ToolBar  drawerToggleClicked={this.drawerToggleClicked} close={this.closeSideDrawerHandler}/>
                <SideDrawer  opened={this.state.showSideDrawer} close={this.closeSideDrawerHandler}/>
                <main className={classes.Main}> 
                    {this.props.children}
                </main>
                <Footer/>
            </AUX>
        );
    }
}

export default Layout ;